<template>
  <div class="loading" ref="loading">
    <div class="content">
      <img src="../assets/logo.png" class="logo"/>
      <img src="../assets/loadProgress.png" class="load">
      <div class="progress">
        <p style="font-size: 50px;">{{progress}}</p>
        <p>%</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      clientHeight: '',
      progress: '50'
    }
  },
  methods: {
  },
  mounted () {
    this.clientHeight = document.documentElement.clientHeight
    this.$refs.loading.style.height = this.clientHeight + 'px'
  }
}
</script>

<style>
  .loading{
    background: url('../assets/loadBg.jpg');
    background-size: 100% 100%;
  }
  .load{
    width: 150px;
    height: 150px;
    position: absolute;
    bottom: 100px;
    left: 50%;
    margin-left: -75px;
  }

  .progress{
    width: 150px;
    height: 150px;
    text-align: center;
    position: absolute;
    bottom: 60px;
    left: 50%;
    margin-left: -75px;
    color: #ffffff;
  }
  /* .content{
    width: 100%;
    height: 100%;
  } */
  .logo{
    margin-top: 20%;
    width: 225px;
    height: 195px;
  }
</style>



