<template>
  <div class="audio">
    <div class="content">
      <!-- <img :src="isplay ? '../assets/pause.png' : '../assets/play.png'" class="playIcon" @click="controlAudio"> -->
      <img v-if="!isplay" src="../assets/play.png" class="playIcon" @click="playAudio">
      <img v-else src="../assets/pause.png" class="playIcon" @click="pauseAudio">
      <div class="playProsess">
        <div class="playFill" :style="{width: playWidth}"></div>
        <!-- <img src="../assets/playBg.png" class="playBg">
        <img src="../assets/playLeft.png" class="playLeft">
        <img src="../assets/playCenter.png" :style="{width: playWidth}" class="playCenter">
        <img src="../assets/playRight.png" :style="{left: playLeft}" class="playRight"> -->
      </div>
      <p>{{audioTime}}</p>
</div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      musicPlay: {},
      audioTime: 0,
      audioInterval: '',
      isplay: false,
      playWidth: 0,
      playLeft: 0
    }
  },
  methods: {
    setAudioInterval () {
      this.audioInterval = setInterval(() => {
        this.getAudioTime()
        if (this.musicPlay.ended) {
          clearInterval(this.audioInterval)
          this.audioTime = 0
          // audioPlayer.currentTime = 0
          this.isPlay = false
        }
        this.musicPlay.paused ? this.isPlay = false : this.isPlay = true
      }, 500)
    },
    playAudio () {
      clearInterval(this.audioInterval)
      this.getAudioTime()
      this.setAudioInterval()
      this.musicPlay.play()
      this.isplay = true
    },
    pauseAudio () {
      this.musicPlay.pause()
      this.isplay = false
    },
    getAudioTime () {
      var audioPlayer = this.musicPlay
      // var audioAllDuration = audioPlayer.duration
      // var currentTime = audioPlayer.currentTime
      this.audioTime = (audioPlayer.currentTime * 100 / audioPlayer.duration)
      this.playLeft = this.audioTime + '%'
      this.playWidth = (this.audioTime) + '%'
    }
  },
  mounted () {
    this.musicPlay = new Audio('../../static/audio/music1.mp3')
    this.setAudioInterval()
  }
}
</script>

<style>
.content{
  background-color: #000000;
}
.playIcon{
  width: 36px;
  height: 36px;
}
/* .playProsess{
  width: 240px;
  height: 8px;
  display: inline-block;
  position: relative;
}
.playBg{
  top: 0;
  left: 0;
  position: absolute;
  width: 240px;
  height: 8px;
}
.playLeft{
  top: 0;
  position: absolute;
  height: 8px;
  left: 0;
}
.playCenter{
  position: absolute;
  height: 8px;
  left: 1.5%;
}
.playRight{
  position: absolute;
  height: 8px;
} */
.playProsess{
  width: 240px;
  height: 8px;
  border-radius: 4px;
  background: -webkit-linear-gradient(top,#B4B4B4,#EFEFEF);
  display: inline-block;
  position: relative;
  box-shadow: 0 0 10px #ffffff;
}
.playFill{
  top: 0;
  position: absolute;
  height: 8px;
  border-radius: 4px;
  left: 0;
  background: -webkit-linear-gradient(top,#65A3BA,#5996D4);
}
</style>



