      </v-touch>
      <!-- <v-touch  v-on:swiperight="swiperright">
        <img src="../assets/right.png" class="wrapper touchRight">
      </v-touch> -->
    <!-- </div> -->
    <div class="menu-container" ref="menuContainer">
      <!-- 这个是内容 -->
      <!-- <img src="../assets/space4.jpg" class="showImg"> -->
      <img :src="explainObj.picture" class="showImg">
      <div class="musicPlayDiv">
        <img v-if="!isplay" src="../assets/play.png" class="playIcon" @click="playAudio">
        <img v-else src="../assets/pause.png" class="playIcon" @click="pauseAudio">
        <div class="playProsess">
          <div class="playFill" :style="{width: playWidth}"></div>
        </div>
      </div>
      <!-- <p class="explainTitle">三维滚环</p>
      <p class="explainText">这个是我们宇航员的模拟训练器，叫三维滚环，它有三个环，每个环围绕一个轴旋转，在三个环的作用力下，这个座椅可以360度旋转。这件展品可以让你了解自身的脑平衡情况及辨别方向的能力。在体验的过程中一定要听从工作人员的指导，系好安全带。
        这个是我们宇航员的模拟训练器，叫三维滚环，它有三个环，每个环围绕一个轴旋转，在三个环的作用力下，这个座椅可以360度旋转。这件展品可以让你了解自身的脑平衡情况及辨别方向的能力。在体验的过程中一定要听从工作人员的指导，系好安全带。
        这个是我们宇航员的模拟训练器，叫三维滚环，它有三个环，每个环围绕一个轴旋转，在三个环的作用力下，这个座椅可以360度旋转。这件展品可以让你了解自身的脑平衡情况及辨别方向的能力。在体验的过程中一定要听从工作人员的指导，系好安全带。
      </p> -->
      <p class="explainTitle">{{explainObj.name}}</p>
      <p class="explainText">{{explainObj.content}}</p>
      <button class="detailBtn" @click="openDetail">查看详情</button>
    </div>
    <div class="mask" v-if="modalFlag" @click="closeDetail"></div>
    <div class="modal" v-if="modalFlag">
      <p class="explainTitle">{{explainObj.name}}</p>
      <p class="modalText">{{explainObj.content}}</p>
      <!-- <p class="explainTitle">三维滚环</p>
      <p class="modalText">这个是我们宇航员的模拟训练器，叫三维滚环，它有三个环，每个环围绕一个轴旋转，在三个环的作用力下，这个座椅可以360度旋转。这件展品可以让你了解自身的脑平衡情况及辨别方向的能力。在体验的过程中一定要听从工作人员的指导，系好安全带。
        这个是我们宇航员的模拟训练器，叫三维滚环，它有三个环，每个环围绕一个轴旋转，在三个环的作用力下，这个座椅可以360度旋转。这件展品可以让你了解自身的脑平衡情况及辨别方向的能力。在体验的过程中一定要听从工作人员的指导，系好安全带。
        这个是我们宇航员的模拟训练器，叫三维滚环，它有三个环，每个环围绕一个轴旋转，在三个环的作用力下，这个座椅可以360度旋转。这件展品可以让你了解自身的脑平衡情况及辨别方向的能力。在体验的过程中一定要听从工作人员的指导，系好安全带。
      </p> -->
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      clientHeight: '',
      musicPlay: {},
      audioTime: 0,
      audioInterval: '',
      isplay: false,
      playWidth: 0,
      count: 0,
      id: '',
      modalFlag: false,
      bgImgUrl: 'url(./static/image/spaceBg.jpg)',
      titleUrl: './static/image/spaceTitle.png',
      explainObj: {},
      artInfoList: []
      // artInfoList: [
      //   {
      //     name: '安全骑行',
      //     content: '展品由单车、电视机和按钮等机构组成。屏幕初始待机画面为安全骑行说明，观众骑上单车后，按下启动按钮，开始出现视频画面，播放的视频是以第一视角穿行在街道卡通动画，当发现屏幕中出现不文明交通行为，例如在马路上玩耍、过马路不走人行横道、闯红灯，撞到行人或者车辆、骑占其他车道等行为，观众迅速按喇叭指出错误，系统会给出反馈，如果没有及时按下喇叭指出，系统会反馈出相应的评价。',
      //     picture: '../assets/space1.png'
      //   },
      //   {
      //     name: '虚拟灭火',
      //     content: '家庭中如有小火灾，常用水灭火，水既能起到冷却降温作用，还能起到窒息作用，所以火很快被扑灭。虚拟灭火演示的就是用水进行灭火的方式，让您在游戏中学习灭火的方法，以便在以后的生活中遇到可控的火灾时能够及时有效的应对和处置。该展品采用虚拟仿真技术、传感器技术以及图形即时生成技术，依据一定的算法，随着灭火的进行程度来改变火焰的效果，在规定时间内如果您扑灭了所有的火焰则演奏动听的乐曲；若失败则播放火灾带来的严重后果画面。',
      //     picture: '../assets/space2.jpg'
      //   },
      //   {
      //     name: '火场逃生',
      //     content: '当遭遇火灾时，人们难免会有恐慌心理，不知所措，甚至会采取错误的逃生方式。火场逃生设置火灾现场模拟室，利用计算机控制灯光，音响、液晶电视显示、烟雾机等设备，形象而逼真的模拟火灾发生时的逃生场景，让您在进行逃生演练的同时，掌握如何选择正确路线、按照正确方法逃生、用湿毛巾或其它纺织物捂住口鼻、半蹲着前行等逃生常识，从而提高您在火灾现场的应变能力和消防意识。',
      //     picture: '../assets/space3.jpg'
      //   },
      //   {
      //     name: '4D电影',
      //     content: '这是我馆4D电影馆，相比较于其他类型影院，具有主题突出、科技含量高、效果逼真、画面冲击性强等特点和优势。4D影院不仅引入震动、坠落、吹风、喷水、挠痒、扫腿等特技，还根据影片的情景精心设计出烟雾、雨、光电、气泡、气味等效果，形成了一种独特的体验。您在观看4D影片时能够获得视觉、听觉、触觉、嗅觉等全方位感受，同时我们这里还有一大批市面上没有的科普类的电影，在您观影的同时，还能了解到很多有趣有用的科普知识。',
      //     picture: '../assets/space4.jpg'
      //   }
      // ]
    }
  },
  methods: {
    swiperleft: function () {
      this.count = this.count + 1
      if (this.count >= this.artInfoList.length) {
        return false
      } else {
        this.$router.push({
          path: 'space',
          query: { count: this.count }
        })
        this.explainObj = this.artInfoList[this.count]
      }
    },
    swiperright: function () {
      if (this.count === 0) {
        return false
      } else {
        this.count = this.count - 1
        this.$router.push({
          path: 'space',
          query: { count: this.count }
        })
        this.explainObj = this.artInfoList[this.count]
      }
    },
    setAudioInterval () {
      this.audioInterval = setInterval(() => {
        this.getAudioTime()
        if (this.musicPlay.ended) {
          clearInterval(this.audioInterval)
          this.audioTime = 0
          this.isPlay = false
        }
        this.musicPlay.paused ? this.isPlay = false : this.isPlay = true
      }, 500)
    },
    playAudio () {
      clearInterval(this.audioInterval)
      this.getAudioTime()
      this.setAudioInterval()
      this.musicPlay.play()
      this.isplay = true
    },
    pauseAudio () {
      this.musicPlay.pause()
      this.isplay = false
    },
    getAudioTime () {
      var audioPlayer = this.musicPlay
      this.audioTime = (audioPlayer.currentTime * 100 / audioPlayer.duration)
      this.playWidth = (this.audioTime) + '%'
    },
    returnHome () {
      this.$router.push({
        path: '/'
      })
    },
    openDetail () {
      this.modalFlag = true
    },
    closeDetail () {
      this.modalFlag = false
    }
  },
  mounted () {
    var that = this
    that.clientHeight = document.documentElement.clientHeight
    that.$refs.space.style.height = that.clientHeight + 'px'
    that.musicPlay = new Audio('./static/audio/music.m4a')
    that.setAudioInterval()
    that.count = that.$route.query.count
    that.id = that.$route.query.id
    if (that.id === 1) {
      that.bgImgUrl = 'url(./static/image/spaceBg.jpg)'
      that.titleUrl = './static/image/spaceTitle.png'
    } else if (that.id === 2) {
      that.bgImgUrl = 'url(./static/image/robotBg.png)'
      that.titleUrl = './static/image/robotTitle.png'
    } else if (that.id === 3) {
      that.bgImgUrl = 'url(./static/image/childBg.jpg)'
      that.titleUrl = './static/image/childTitle.png'
    } else if (that.id === 4) {
      that.bgImgUrl = 'url(./static/image/informationBg.jpg)'
      that.titleUrl = './static/image/informationTitle.png'
    } else if (that.id === 5) {
      that.bgImgUrl = 'url(./static/image/basicBg.jpg)'
      that.titleUrl = './static/image/basicTitle.png'
    } else if (that.id === 6) {
      that.bgImgUrl = 'url(./static/image/lifeBg.jpg)'
      that.titleUrl = './static/image/lifeTitle.png'
    } else if (that.id === 7) {
      that.bgImgUrl = 'url(./static/image/safetyBg.jpg)'
      that.titleUrl = './static/image/safetyTitle.png'
    } else if (that.id === 8) {
      that.bgImgUrl = 'url(./static/image/cinemaBg.jpg)'
      that.titleUrl = './static/image/cinemaTitle.png'
    }
    that.httpPost({
      url: '/api/query_all_second',
      data: {
        id: that.id
      },
      successBack: function (data) {
        // alert(data);
        if (data.retCode === 1) {
          // alert(data)
          that.artInfoList = data.list
          that.explainObj = that.artInfoList[that.count]
        } else {
          that.tips = '提示：' + data.msg
        }
      }
    })
    // document.addEventListener('click', function (e) {
    //   alert(2222)
    //   if (!!that.$refs.modal.contains(e.target)) {
    //     alert(33333)
    //     return
    //   } else {
    //     alert(1111)
    //     that.modalFlag = false
    //   }
    // })
  }
}
</script>

<style>
.space{
  /* background: url('../assets/spaceBg.jpg'); */
  /* background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed; */
  background-size: 100% 100%;
}
.spaceTitle{
  margin-top: 8%;
  width: 210px;
  height: 57px;
}
.wrapper{
  width: 50px;
  height: 55px;
}
.touchDiv{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
}
.touchLeft{
  position: absolute;
  left: 1%;
  top: 24%;
}
.touchRight{
  position: absolute;
  right: 1%;
  top: 24%;
}
.showImg{
  margin-top: 10%;
  width: 240px;
  height: 154px;
  border: 5px solid #ffffff;
  box-shadow: 0 0 20px #ffffff;
}
.explainTitle{
  font-weight: 500;
  text-shadow: 0 0 10px #ffffff;
  margin-top: 60px;
  font-size: 24px;
  color: #ffffff;
}
.explainText{
  font-size: 13px;
  color: #ffffff;
  margin: 23px 55px;
  line-height: 20px;
  text-align: left;
  max-height: 200px;
  text-indent: 2em;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 10;/*显示几行*/
  overflow: hidden;
}
.playIcon{
  width: 36px;
  height: 36px;
}
.playProsess{
  margin-bottom: 15px;
  width: 230px;
  height: 8px;
  border-radius: 4px;
  background: -webkit-linear-gradient(top,#B4B4B4,#EFEFEF);
  display: inline-block;
  position: relative;
  box-shadow: 0 0 10px #ffffff;
}
.playFill{
  top: 0;
  position: absolute;
  height: 8px;
  border-radius: 4px;
  left: 0;
  background: -webkit-linear-gradient(top,#65A3BA,#5996D4);
}
.musicPlayDiv{
  top: 39%;
  left: 12%;
  position: absolute;
  z-index: 199;
}
.return{
  height: 50px;
  width: 55px;
  position: absolute;
  left: 1%;
  top: 1%;
  z-index:199;
}
.detailBtn{
  height:25px;
  width: 100px;
  background-color: rgba(255, 255, 255, 0.5);
  color: #ffffff;
  font-size:10px;
  border:none;
  position: fixed;
  bottom: 3%;
  left: 50%;
  margin-left: -50px;
  z-index:199;
}
.mask{
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: #000;
  z-index: 299;
  opacity: 0.5;
}
.modal{
  width: 300px;
  height: 446px;
  position: absolute;
  left: 50%;
  margin-left: -150px;
  top: 50%;
  margin-top: -223px;
  text-align: center;
  z-index: 399;
  background: url('../assets/modalImg.png') no-repeat;
  background-size: 100% 100%;
}
.modalText{
  font-size: 13px;
  color: #ffffff;
  margin: 23px 55px;
  line-height: 20px;
  text-align: left;
  max-height: 260px;
  text-indent: 2em;
  overflow-y: auto;
}
</style>



