<template>
  <div class="test" ref="test">
    <ul class="b1">
      <li v-for="(item, index) in menu" :key="index"   @click="show(index)">
      <div>{{item.name}}</div>
      <ul :class ='{active:flag[index]}'>
          <li  v-for="(item, index) in item.children" :key="index">
              {{item.name}}
          </li>
      </ul>
    </li>
  </ul>
  </div>
</template>
<script>
export default {
  data () {
    return {
      clientHeight: '',
      flag: [],
      menu: [{
        name: 'menu1',
        children: [{
          name: 'children1'
        }, {
          name: 'children1'
        }]
      }, {
        name: 'menu2',
        children: [{
          name: 'children2'
        }, {
          name: 'children2'
        }]
      }, {
        name: 'menu3',
        children: [{
          name: 'children3'
        }, {
          name: 'children3'
        }]
      }
      ]
    }
  },
  methods: {
    show (index) {
      alert(1111)
      this.flag[index] = !this.flag[index]
      this.$set(this.flag, index, this.flag[index])
    }
  },
  mounted () {
    this.clientHeight = document.documentElement.clientHeight
    this.$refs.test.style.height = this.clientHeight + 'px'
  }
}
</script>

<style>
  .test{
    background: url('../assets/pig.jpg');
    background-size: 100% 100%;
  }
</style>



