// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
import VueTouch from 'vue-touch'
import axios from 'axios'
import urlHeader from '../config/urlHeader'

Vue.use(VueAwesomeSwiper)

Vue.use(VueTouch, {name: 'v-touch'})

Vue.config.productionTip = false
Vue.prototype.axios = axios

VueTouch.config.swipe = {
  threshold: 100 // 手指左右滑动距离
}

/**
 *
 * opts : url 接口名
 *        data 上传的数据
          config 上传的其他配置 例如 请求头等
          backErrorInfo 接口调用成功，但是后台校验没有通过 报错信息
          errorInfo  接口调用失败提示信息
          successBack 成功的回调函数 接收一个参数 data 成功返回的数据
 */
Vue.prototype.httpPost = function (opts) {
  let that = this

  that.axios
    .post(urlHeader.header + opts.url, opts.data || {}, opts.config || {})
    .then(
      response => {
        // success callback
        let res = response.data
        if (opts.successBack) {
          opts.successBack(res)
        }
      },
      response => {
        // error callback
        console.log(opts.errorInfo || '数据请求失败')
      }
    )
}
Vue.prototype.httpGet = function (opts) {
  let that = this
  that.axios.get(urlHeader.header + opts.url, opts.config || {}).then(
    response => {
      // success callback
      let res = response.data
      if (opts.successBack) {
        opts.successBack(res)
      }
    },
    response => {
      // error callback
      console.log(opts.errorInfo || '数据请求失败')
    }
  )
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



